body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#header {
    padding: 0 5%;
}

#header, #headerRight {
    display: -webkit-flex;
    display: flex;
}

#headerLeft {
    -webkit-flex: 1 1;
            flex: 1 1;
}

#headerRight {
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

#headerRight > button {
    margin-left: 20px;
}

.user-list-head {
    text-align: center;
    margin: 0;
}

.user-list {
    padding: 0 5%;
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.user-list > li {
    padding: 7px 10px;
    border-radius: 8px;
    background-color: aquamarine;
    display: inline-block;
    margin: 3px;
    cursor: pointer;
}

.chatbox-container {
    padding: 0 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

#chatbox {
    margin: 0 15px;
}

#forms-container {
    padding: 0 5%;
    display: -webkit-flex;
    display: flex;
}

#sign-up-form, #sign-in-form {
    text-align: center;
}


